import React from 'react'
import '../StyleSheets/Title.css';
import general from '../utils/general';
import { Link } from "react-router-dom";

import TextField from '@material-ui/core/TextField';
import FormLabel from '@material-ui/core/FormLabel';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import FlickGameRules from './FlickGameRules';


class CreateGameForm extends React.Component {
    constructor(props) {

      
      super(props);

      console.log(props)
      
      const cookies_token = general.getToken();
      if(props.token) {
        this.state = {username: props.token.split('_')[1], gameCreated: true, gameCode: props.token.split('_')[0], indToken:  props.token, errorMsg : ""};

      } else if(general.hasToken()) {
        
        this.state = {username: cookies_token.split('_')[1], gameCreated: true, gameCode: cookies_token.split('_')[0], indToken:  cookies_token, errorMsg : ""};

      } else {
        this.state = {username: '', gameCreated: false, gameCode: "", indToken: "", errorMsg : ""};

      }

      
  
      this.handleChange = this.handleChange.bind(this);
      this.handleGuessLink = this.handleGuessLink.bind(this);
      this.handleSubmit = this.handleSubmit.bind(this);
    }

    componentWillReceiveProps(props) {
      
      if(props.token) {
        this.setState({username: props.token.split('_')[1], gameCreated: true, gameCode: props.token.split('_')[0], indToken:  props.token, errorMsg : ""});
      }
    }
  
    handleGuessLink(e) {

      general.setRules("Guess The Soju Bottle No. Rules", [" In 'Guess The Soju Bottle No.' you take turns to guess what the number under the virutal Soju bottle cap is. The number under the virutal Soju bottle cap is between 0 and 100.",
      " Each guess you make must be within the previous highest and lowest guesses. If you make an incorrect guess the computer will tell you whether the number is higher or lower and update the previous higher or lower guess.",
      "If you make a correct guess you drink!"]);

        general.startGuessGame(this.state.indToken)
        .then(data => {
            if(!data.isValid) {
                alert(data.validMsg);
                e.preventDefault();

            }
        })
        console.log("hey");
    }

    handleFlickLink(e) {
      general.setRules("Flick the Soju Bottle Cap Game", ["In 'Flick the Soju Bottle Cap Game' you take turns tring to flick the tail off the virtual soju bottle cap, by pressing the hit button.",
      "The closer the slider is to the red area, the harder your flick is.",
      "If you flick the tail off the virtual soju bottle cap, the people before and after you drink!"]);
 


      general.startFlickGame(this.state.indToken)
      .then(data => {
          if(!data.isValid) {
              alert(data.validMsg);
              e.preventDefault();

          }
      })
      console.log("hey");


    }


    handleBlackoutLink(e) {

      general.startBlackout(this.state.indToken)
      .then(data => {
          if(!data.isValid) {
              alert(data.validMsg);
              e.preventDefault();

          }
      })
      console.log("hey");
    }

    handleChange(event) {
      this.setState({username: event.target.value});
    }
  
    handleSubmit(event) {
        event.preventDefault();
        console.log("hey");
        general.createGame(this.state.username)
        .then(data => {

            if(data.isValid) {
              this.setState({errorMsg: ""});
              
              this.setState({gameCode: data.gameToken, indToken: data.individualToken});
              this.props.passToken(data.individualToken);
              this.setState({gameCreated: true}); 

            } else {
              this.setState({errorMsg: data.validMsg});
            }
            
        })

           
    }


  
    render() {

        if(!this.state.gameCreated) {
            return (

              <React.Fragment  >

                    <Typography variant="h6" gutterBottom>
                    Create Game
                    </Typography>
                    <Grid container spacing={3}>
                      <Grid item xs={12}>
                        <TextField 
                        id={this.state.errorMsg == "" ? "standard-basic" : "standard-error-helper-text"}
                        value={this.state.username} 
                        onChange={this.handleChange} 
                        helperText={this.state.errorMsg}
                        label="Username" 
                        />

                      </Grid>

                      <Grid item xs={12}>
                        <Button
                          variant="contained"
                          color="primary"
                          onClick={this.handleSubmit}   
                        >
                          Submit
                        </Button>

                      </Grid>
                    </Grid>

                    
                </React.Fragment>

              );
        } else {
            return (
              <React.Fragment  >
                 <Grid container spacing={3} justifyContent = "flex-end">
                  <Grid item xs style = {{"max-width" : "27%"}}>
                    <Button variant="contained" color="primary" style = 
                          {{"background-color": "#3D3D90", "max-width": "90%", "display" : "block",
                          "align-self":"center", 
                          "margin-bottom":"1rem",
                          "margin-left": "auto",
                          "margin-right": "auto"}} 
                          onClick={() => {navigator.clipboard.writeText(this.state.gameCode)}}
                        >
                              <div className = "smallText">
                                Copy Game Code

                              </div>
                          
                         
                        </Button>
                  </Grid>
                 
                </Grid>
                <Grid container spacing={3}>

                  <Grid item xs={12}>
                    <div className="titleText">Your game code is {this.state.gameCode} </div> 
                      

                  </Grid>
                    <Grid item xs={12}>
                      <Link 
                        to={{pathname: "/guessNumberGame",token:this.state.indToken}} 
                        className="link" 
                        onClick={(e) => this.handleGuessLink(e)}
                      >
                        <Button variant="contained" color="primary" style = 
                          {{"background-color": "#3D3D90", "max-width": "90%", "display" : "block",
                          "align-self":"center", 
                          "margin-bottom":"1rem",
                          "margin-left": "auto",
                          "margin-right": "auto"}} 
                        >
                          <Typography variant="button">
                              Start Guess Bottle No. Game
                          </Typography>
                         
                        </Button>
                      </Link>


                      <Link 
                        to={{pathname: "/flickGame",token:this.state.indToken}} 
                        className="link" 
                        onClick={(e) => this.handleFlickLink(e)}
                      >
                        <Button variant="contained" color="primary" style = 
                          {{"background-color": "#3D3D90", "max-width": "90%", "display" : "block",
                          "align-self":"center", 
                          "margin-bottom":"1rem",
                          "margin-left": "auto",
                          "margin-right": "auto"}} 
                        >
                          <Typography variant="button">
                              Start Flick the Soju Bottle Cap Game
                          </Typography>
                         
                        </Button>
                      </Link>

                      <Link 
                        to={{pathname: "/blackout",token:this.state.indToken}} 
                        className="hidden" 
                        onClick={(e) => this.handleBlackoutLink(e)}
                      >
                        <Button variant="contained" color="primary" style = 
                          {{"background-color": "#3D3D90", "max-width": "90%", "display" : "block",
                          "align-self":"center", 
                          "margin-bottom":"1rem",
                          "margin-left": "auto",
                          "margin-right": "auto"}} 
                        >
                          <Typography variant="button">
                              Start Blackout
                          </Typography>
                         
                        </Button>
                      </Link>

                  </Grid>
                </Grid>
              </React.Fragment>
                
              );
        }
      
    }
  }

export default CreateGameForm

